.modal {
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(130, 151, 130, 0.4);
    position: fixed;
    
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;

    pointer-events: none; 
    transition: var(--transition);
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modal_window {
    padding: 5px;
    border-radius: 8px;
    background-color: var(--color-secondary);
    border: 3px solid var(--color-primary);
    transition: 0.4s all;
}

.modal_window.active {
    transform: scale(1);
}
.info_box {
    padding: 20px;
    /* width: 100%; */
    /* height: 100%; */
    color:var(--color-primary);
    text-align: center;

    display: flex;
    flex-direction: column;
}

.info_box p{
    padding-left: 35px;
    padding-right: 35px;
    padding-bottom: 20px;

    font-size: 20px;
    font-weight: var(--weight-bold);
}

.close_btn {
    color: var(--color-primary);
    align-self: flex-end; 
    font-size: 30px;
    cursor: pointer;
}