.sale_list_section{
    margin: 0 30px 94px;
} 

.sale_list_title {
    margin: 49px 0 10px;
    font-weight: var(--weight-bold);
    font-size: 40px;
}

@media (max-width: 1100px) {
	.categories_list {
		grid-template-columns: repeat(3, 1fr);
		gap: auto;
	}
}
@media (max-width: 500px) {
	.sale_list_section {
		display: flex;
    	flex-direction: column; 
		justify-content: center;
	}
	.sale_list_title{
    justify-content: center;
		font-size: 30px;
	}
}

