.category_item {
    overflow: hidden;

}

.category_item:hover{
    scale: 1.1;
}

.category_image {
    margin-bottom: 20px;

    width: 100%;
    height: 350px;
    
    display: block;
    object-fit: cover;
}

.category_title {
    font-weight: var(--weight-medium);
    font-size: 18px;
    line-height: 130%;
    letter-spacing: 0.54px;

    text-align: center;
}

@media (max-width:930px) {
    .category_image {
        height: 250px;
    }
}
@media (max-width:500px) {
    .category_title {
        font-size: 15px;
    }
}